<template>
    <div class="recommendMatching">
        <div class="title">推荐配置</div>
        <div class="nav">
            <el-button size="mini" type="primary" @click="openDilog">新增</el-button>
            <el-button size="mini" type="primary" @click="getRecommendSkillList" class="el-icon-refresh">刷新</el-button>
            <span>标签名称：</span>
            <el-input
                class="inputC"
                placeholder="请输入标签名称"
                v-model="tagName"
                size="mini"
                clearable>
            </el-input>
            <span>意图名称：</span>
            <el-input
                class="inputC"
                placeholder="请输入意图名称"
                v-model="selectValue"
                size="mini"
                clearable>
            </el-input>
            <span>创建时间：</span>
            <el-date-picker
                v-model="ctime"
                class="inputC"
                size="mini"
                type="date"
                placeholder="选择日期"
                value-format="yyyy-MM-dd">
            </el-date-picker>
            <span>修改时间：</span>
            <el-date-picker
                v-model="utime"
                class="inputC"
                size="mini"
                type="date"
                placeholder="选择日期"
                value-format="yyyy-MM-dd">
            </el-date-picker>
            <el-button size="mini" type="primary" @click="getRecommendSkillList">搜索</el-button>
        </div>
        <div class="table-con">
            <el-table
                v-loading="loading"
                border
                :data="tableData"
                style="width: 100%"
                :row-class-name="tableRowClassName">
                <el-table-column
                    label="序号"
                    align="center"
                    type="index"
                    width="70">
                </el-table-column>
                <el-table-column
                    prop="tagName"
                    label="标签名称"
                    width="280">
                </el-table-column>
                <el-table-column
                    prop="intention"
                    label="意图名称"
                    width="280">
                </el-table-column>
                <el-table-column
                    label="重要程度">
                    <template slot-scope="scope">
                        <el-input v-model="scope.row.sort" placeholder="请输入" @blur="handleChange(scope.row)"
                                  size="mini"></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="creatorName"
                    label="创建人">
                </el-table-column>
                <el-table-column
                    prop="createTime"
                    label="创建时间">
                </el-table-column>
                <el-table-column
                    prop="updateName"
                    label="修改人">
                </el-table-column>
                <el-table-column
                    prop="updateTime"
                    label="修改时间">
                </el-table-column>
                <el-table-column
                    align="center"
                    label="是否启用">
                    <template slot-scope="scope">
                        <el-switch
                            @change="switchChange(scope.row)"
                            :value="scope.row.isUsed==='1'?true:false"
                            active-color="#13ce66"
                            active-text="启用"
                            inactive-text="停用"
                            inactive-color="#ff4949">
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column
                    align="center"
                    width="120"
                    label="操作">
                    <template slot-scope="scope">
                        <el-link type="primary" @click="editOpen(scope.row)">修改</el-link>
                        <el-divider direction="vertical"></el-divider>
                        <el-link type="danger" @click="deleteRecommendSkillList(scope.row)">删除</el-link>
                    </template>
                </el-table-column>
            </el-table>
            <div class="block">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pages.pageNo"
                    :page-sizes="[8, 10, 20, 50]"
                    :page-size="pages.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pages.total">
                </el-pagination>
            </div>
        </div>
        <el-dialog
            :title="dialogTit"
            :visible.sync="dialogVisible"
            top="5vh"
            width="80%">
            <div class="dialog-con">
                <el-row :gutter="20">
                    <el-col :span="7">
                        <div class="left">
                            <div class="title">选择标签名称</div>
                            <div style="margin-top: 10px">
                                <el-input
                                    style="width: calc(100% - 66px)"
                                    placeholder="输入关键字进行过滤"
                                    size="medium"
                                    v-model="filterText2">
                                </el-input>
                                <el-button @click="selectTree(filterText2)" icon="el-icon-search" size="medium"></el-button>
                                <!--                     default-expand-all-->
                                <el-tree
                                    :style="treeStyleObject"
                                    v-loading="treeLoading"
                                    v-show="!showSelectTree"
                                    class="filter-tree"
                                    :filter-node-method="filterNode"
                                    ref="tree"
                                    :props="defaultProps"
                                    :load="loadNode"
                                    :highlight-current="true"
                                    :expand-on-click-node="false"
                                    @node-click="nodeClick"
                                    lazy>
                                <span class="custom-tree-node"  slot-scope="{ node, data }">
                                  <span>
                                        <i v-if="data.type==='tagobj'" class="el-icon-office-building"></i>
                                        <i v-else-if="data.type==='index'" class="el-icon-folder-opened"></i>
                                        <i v-else-if="data.type==='tag'" class="el-icon-price-tag"></i>
                                      {{ node.label }}
                                  </span>
                                </span>
                                </el-tree>
                                <el-tree
                                    :style="{treeStyleObject}"
                                    v-loading="treeLoading"
                                    v-show="showSelectTree"
                                    :data="dataTree"
                                    default-expand-all
                                    :props="defaultProps2"
                                    :expand-on-click-node="false"
                                    @node-click="nodeClick"
                                    :highlight-current="true">
                                <span class="custom-tree-node"  slot-scope="{ node, data }">
                                  <span>
                                        <i v-if="data.type==='tagobj'" class="el-icon-office-building"></i>
                                        <i v-else-if="data.type==='index'" class="el-icon-folder-opened"></i>
                                        <i v-else-if="data.type==='tag'" class="el-icon-price-tag"></i>
                                      {{ node.label }}
                                  </span>
                                </span>
                                </el-tree>
                            </div>
                        </div>
                    </el-col>
                    <el-col :span="7">
                        <div class="center">
                            <div class="title">已选值</div>
                            <div class="tag" v-show="tagNameSelect">标签名称： {{tagNameSelect}}</div>
                            <div class="tag" v-show="skillName">意图名称： {{skillName}}</div>
                        </div>
                    </el-col>
                    <el-col :span="10">
                        <div class="right">
                            <div class="title">选择意图名称</div>
                            <span class="tag" v-for="item in skillList" :key="item.id" @click="changeSkillValue(item)">{{item.intention}}</span>
                            <div class="block">
                                <el-pagination
                                    @size-change="handleSizeChange2"
                                    @current-change="handleCurrentChange2"
                                    :current-page="pages2.pageNo"
                                    :page-sizes="[8, 10, 20, 50]"
                                    :page-size="pages2.pageSize"
                                    layout="total, sizes, prev, pager, next, jumper"
                                    :total="pages2.total">
                                </el-pagination>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false" size="mini">取 消</el-button>
                <el-button type="primary" size="mini" @click="editSkillList">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
  import Interface from '@/base/mixin/user-behavior';

  export default {
    name: "recommendMatching",
    mixins: [Interface],
    components: {},
    props: {},
    data() {
      return {
        treeStyleObject:{
          maxHeight:460+'px',
          overflow:'scroll'
        },
        doHeight: 460,
        bbbbbbbbb: '1',
        tagName: '',
        selectValue: '',
        tableData: [],
        pages: {
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
        pages2: { //新增时下拉列表分页
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
        id:'',//编辑推荐配置id
        loading: false,
        dialogTit: '新增',
        skillName: '',//选中技能名称
        skillGroupId: '',//技能id
        tagNameSelect: '测试',//选中标签名称
        tagId: 'eeee',//选中标签id
        dialogVisible: false,
        skillList: [],
        //标签树
        defaultProps: {
          children: 'children',
          label: 'text',
          isLeaf: 'leaf'
        },
        defaultProps2: {
          children: 'children',
          label: 'text',
        },
        filterText2: '',
        treeLoading:false,
        showSelectTree:false,
        dataTree: [],

        node: [],
        resolve: [],
        numb:1,

        utime:'',
        ctime:'',
      }
    },
    mounted() {
      this.getRecommendSkillList()
    },
    methods: {
      switchChange(v){ //开关是否启用
        for (let i = 0;i<this.tableData.length;i++){
          if (this.tableData[i].id === v.id){
            if (v.isUsed==='1'){
              this.tableData[i].isUsed = '2'
            }else if (v.isUsed==='2'){
              this.tableData[i].isUsed = '1'
            }
            this.switchModifyIsUse(this.tableData[i].id,this.tableData[i].isUsed)
            window.console.log(v)
            return
          }
        }
      },
      //是否启用接口
      switchModifyIsUse(id,isUsed) {
        this.modifyIsUse({
          "id":id,
          "isUsed":isUsed
        }).then(res => {
        }).catch(error => {
          window.console.log(error)
        })
      },
      handleSizeChange(val) {
        this.pages.pageSize = val
        this.getRecommendSkillList()
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.pages.pageNo = val
        this.getRecommendSkillList()
        console.log(`当前页: ${val}`);
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex % 2 === 0) {
          return 'success-row';
        }
        return '';
      },
      //推荐配置列表
      getRecommendSkillList() {
        this.loading = true
        this.getIntentionTagData({
          "intention": this.selectValue,
          "tagName": this.tagName,
          "current": this.pages.pageNo,
          "size": this.pages.pageSize,
          "ctime":this.ctime,
          "utime":this.utime,
        }).then(res => {
          this.loading = false
          this.tableData = res.list
          this.pages.total = res.total
        }).catch(error => {
          window.console.log(error)
        })
      },
      openDilog() { //新增
        this.dialogTit = '新增'
        this.skillName = ''
        this.skillGroupId = ''
        this.tagNameSelect = ''//选中标签名称
        this.tagId = ''//选中标签id
        this.dialogVisible = true;
        this.getSkillList()
      },
      editOpen(row) {//编辑
        this.dialogTit = '修改'
        this.skillName = row.intention
        this.skillGroupId = row.intentionId
        this.tagNameSelect = row.tagName
        this.tagId = row.tagId
        this.dialogVisible = true;
        this.id = row.id
        this.getSkillList()
      },
      editSkillList() {  //新增或者修改
        if (this.skillName===''||this.tagNameSelect===''){
          this.$message.warning('请选择标签和意图名称')
          return;
        }
        if (this.dialogTit === '新增') {
          this.addIntentionTag({
            "intention": this.skillName,
            "intentionId": this.skillGroupId,
            "tagId": this.tagId,
            "tagName": this.tagNameSelect,
          }).then(() => {
            this.getRecommendSkillList()
            this.dialogVisible = false;
          }).catch(error => {
            window.console.log(error)
          })
        } else {
          this.updateIntentionTag({
            "tagId": this.tagId,
            "tagName": this.tagNameSelect,
            "intention": this.skillName,
            "intentionId": this.skillGroupId,
            "id": this.id,
          }).then(() => {
            this.getRecommendSkillList()
            this.dialogVisible = false;
          }).catch(error => {
            window.console.log(error)
          })
        }
      },
      //删除
      deleteRecommendSkillList(row) {
        this.deleteIntentionTag({
          "id": row.id,
        }).then(() => {
          this.getRecommendSkillList()
        }).catch(error => {
          window.console.log(error)
        })
      },
      handleChange(row) {
        this.modifySort({
          "id": row.id,
          "sort": row.sort,
        }).then(() => {
          this.getRecommendSkillList()
        }).catch(error => {
          window.console.log(error)
        })
      },
      //技能配置列表--新增时的下拉列表
      getSkillList() {
        this.getIntentionData({
          "intention":'',
          'welcome': "",
          "current": this.pages2.pageNo,
          "size": this.pages2.pageSize,
        }).then(res => {
          this.skillList = res.list
          this.pages2.total = res.total
        }).catch(error => {
          window.console.log(error)
        })
      },
      handleSizeChange2(val) {
        this.pages2.pageSize = val
        this.getSkillList()
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange2(val) {
        this.pages2.pageNo = val
        this.getSkillList()
        console.log(`当前页: ${val}`);
      },
      //点击选择
      changeSkillValue(item){
        window.console.log(item)
        this.skillName = item.intention
        this.skillGroupId = item.id
      },
      nodeClick(a) {
        window.console.log('message', a);
        this.tagId = a.entryId;
        this.tagNameSelect = a.name;
        // 这里有三个参数
      },
      //标签树
      selectTree(keywords) {
        this.treeLoading = true;
        let keywordTemp = ''
        if (keywords!=''&&keywords!=null){
          keywordTemp = keywords
          this.showSelectTree = true
          this.value = ''
        }else {
          this.showSelectTree = false
          keywordTemp = this.value
          this.filterText2 = ''
        }
        this.node.childNodes = [];
        this.getBdaTagList({
          keyword: keywordTemp,
          id: 0,
        }).then(res => {
          this.dataTree = res
          this.treeLoading = false;
          this.resolve(this.dataTree)
        }).catch(() => {

        });
        //手动调用加载数据方法
        // this.loadNode(this.node, this.resolve);
      },
      filterNode(value, data) {
        if (!value) return true;
        return data.text.indexOf(value) !== -1;
      },
      loadNode(node, resolve) {

        //加载数据的方法里把node，resolve存起来 ==== 只存初始化时的数据
        if (this.numb===1){
          this.node = node;
          this.resolve = resolve;
          // 把子节点清空，否则下次加载时会直接往里push子节点导致重复
          this.node.childNodes = [];
          this.numb++
        }
        // let permissionArr =[]
        // permissionArr = ['1','4']

        this.dataTree = []
        let $this = this
        if (node.data) {
          if (node.data.type) {
            this.getBdaTagList(
              {
                id: node.data.entryId,
                type: node.data.type,
                //filterStatus:[3,5,7], //[1,2,4,6],//过滤除了“入库3”、“停用5”、“下线7”的标签；
                // filterTagRuleTypes: [3, 4]//过滤其他标签，只要大数据平台  1==大数据平台，3==手工标记 ，4==人工智能平台
              }).then(res => {
              $this.dataTree = res
              $this.dataTree.forEach(item => {
                if (item.type == 'tag') {
                  item.leaf = true
                }
              })
              resolve($this.dataTree)
            }).catch(() => {

            });
          } else {
            resolve([])
          }

        } else {
          let params = {
            // keyword:'11',
            //filterStatus: ["7"],//过滤下线标签
            // filterTagRuleTypes: qs.stringify( permissionArr,{ arrayFormat: "repeat" })//过滤其他标签，只要手工标签
          };
          this.getBdaTagList(params).then(res => {
            $this.options = res
            for (let i = 0; i < res.length; i++) {
              res[i].disabled = true;
            }
            this.dataTree = res
            resolve(this.dataTree)
          }).catch(() => {

          });

        }
      },
    }
  }
</script>

<style scoped lang="scss">
    .recommendMatching {
        ::v-deep .el-table {
            .success-row {
                background: rgb(244, 244, 244);
            }

            .cell {
                padding: 2px 4px;
            }

            th {
                background-color: rgb(24, 153, 250);

                .cell {
                    color: #ffffff;
                }
            }
        }

        .inputC {
            width: 200px;
            margin: 0px 6px
        }

        & > div {
            margin: 10px 0px;
        }

        .title {
            padding-left: 18px;
            border-left: 6px solid #1A84F4;
            font-weight: bold;
        }

        .nav {
            span {
                margin: 0px 2px 0px 30px;
            }
        }
        .dialog-con{
            padding: 10px 20px;
            min-height: 380px;
            .title{
                border-left: 6px solid #1A84F4;
                padding-left: 20px;
                margin-bottom: 16px;
            }
            .right,.center{
                .tag{
                    display: inline-block;
                    width: calc(50% - 16px - 12px);
                    line-height: 28px;
                    margin: 10px 8px;
                    padding: 6px;
                    border-radius: 4px;
                    color: #ffffff;
                    cursor: pointer;
                    text-align: center;
                    background: url(../../assets/img/1.png) no-repeat center;
                }
                .tag:hover{
                    box-shadow: 1px 0px 4px #1A84F4;
                }
            }
            .center{
                .tag{
                    width: calc(100% - 16px - 12px);;
                    margin: 10px 8px;
                    background: url(../../assets/img/2.png) no-repeat center;

                }
                &>.tag:nth-child(3){
                    margin-top: 100px;
                }
                .tag:hover{
                    box-shadow: 1px 0px 4px #1A84F4;
                }
            }
        }
    }
</style>